.modalBackground {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.7);
  z-index: 3;
  display: flex;
  justify-content: center;
  align-items: center;
}

.modal {
  z-index: 4;
  width: 600px;
  max-width: 90vw;
  max-height: 80vh;
  position: fixed;
  left: calc(50vw - 300px);
  display: flex;
  overflow: hidden;
  justify-content: center;
}

.close.absolutePosition {
  z-index: 9999;
  position: absolute;
  top: 16px;
  right: 16px;
  color: lightcyan;
  font-size: 48px;
  cursor: pointer
}

.opaque {
  background-color: #f1f1f1;
  border-radius: 16px;
  padding: 32px;
  justify-content: center;
  flex-direction: column;
  text-align: center;
}

.opaque > img {
  max-height: 50vh;
  max-width: 540px;
  width: auto;
  height: auto;
}

.opaque > form > select,
.opaque > form > input {
  border-radius: 24px;
  border: 1px solid darkgray;
  padding: 8px 16px;
  font-size: 24px;
  margin-top: 24px;
  max-width: 100%;
}

.opaque > form > select {
  width: 256px;
  background-color: white;
}

.opaque > form > a {
  text-decoration: underline;
  color: #4caf50;
  cursor: pointer;
  display: block;
  margin-top: 12px;
}

.opaque button,
.opaque .button {
  width: 100%;
  text-align: center;
  background-color: #4caf50;
  color: white;
  font-weight: bold;
  border-radius: 16px;
  padding: 8px;
  margin-top: 24px;
  font-size: 20px;
  cursor: pointer;
}

.modal p {
  margin: 0;
}

.modal video {
  max-width: 100%;
}

.modal .close {
  position: absolute;
  top: 8px;
  right: 8px;
  font-weight: bold;
  font-size: 24px;
  width: min-content;
  color: #444;
  cursor: pointer;
}

.modal button > img {
  height: 32px;
}

img.logo {
  width: 50%;
  margin-left: 25%;
}

.qr {
  margin: auto;
  width: 50vh !important;
}

.saleType {
  display: flex;
  width: 100%;
  justify-content: center;
}

.saleType > label {
  margin-top: 16px;
  background-color: white;
  color: #4caf50;
  border: 2px solid #4caf50;
  font-size: 16px;
  padding: 4px;
  cursor: pointer;
}

input[type="radio"]:checked + label {
  background-color: #4caf50;
  color: #fff;
  border: 1px solid #4caf50;
  font-size: 16px;
}

.saleType > input {
  display: none;
}

.saleType label:first-of-type {
  border-radius: 16px 0 0 16px;
  padding-left: 12px;
}

.saleType label:last-of-type {
  border-radius: 0 16px 16px 0;
  padding-right: 12px;
  border-left: 0;
}

@media screen and (max-width: 600px) {
  .modal {
    left: 5vw;
    padding: 8px;
    max-height: 95vh;
  }
}
